/* Main styles */

main {
  width: 100%;
  height: auto;
  padding: 30px 12.5%;
}

mark {
  background: #8fbcbb;
  color: #f8f9fb;
  padding: 1px;
  border-radius: 2px;
}

.main-container {
  width: 100%;
  height: auto;
  min-height: 500px;
  padding: 10px;
  border-radius: 3px;
  background: #434c5e;
}

.main-item {
  width: 100%;
  height: 100%;
  padding: 0 10px;
  font-family: "Roboto Mono";
  font-weight: 600;
}

/* TP-Styles */

.tp-title {
  font-size: 25px;
  color: #e5e9f0;
}

.tp-link {
  font-size: 16px;
  margin-right: 20px;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-link:hover {
  text-shadow: 0 0 8px #e5e9f0;
}

.tp-link svg {
  margin-right: 5px;
}

.tp-image_block {
  margin: 30px 0 40px 0;
}

.tp-image {
  border-radius: 8px;
  width: 100%;
}

.tp-text {
  font-size: 18px;
  color: #d8dee9;
  white-space: pre-line;
}

.tp-sublinks_block {
  width: 100%;
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
  place-items: center;
  margin-top: 10px;
}

.tp-sublink {
  color: #d8dee9;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-sublink:hover {
  text-shadow: 0 0 8px #d8dee9;
}

.tp-sublink::before {
  content: "#";
  display: inline-block;
  margin-right: 4px;
}

/* TP-Code styles */

.tp-code_block {
  width: 100%;
  height: auto;
  display: block;
}

.tp-code_container {
  width: 100%;
  height: auto;
}

.tp-code_box {
  padding: 26px 5px 5px 5px;
  background: #3b4252;
  border-radius: 8px;
  position: relative;
  border: 2px solid #4c566a;
  box-shadow: 0px 0px 8px 0 #4c566a;
  margin-bottom: 20px;
}

.tp-code_list {
  display: block;
  border-radius: 5px;
  width: 100%;
  background: transparent;
  padding: 10px 10px 20px 10px;
  overflow-x: hidden;
}

.dots {
  position: absolute;
  left: 8px;
  top: 8px;
  display: grid;
  width: 52px;
  grid-template: 1fr / repeat(3, 1fr);
  grid-gap: 8px;
}

.dot-red,
.dot-yellow,
.dot-purple {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s linear;
}

.dot-red {
  background: #bf616a;
}

.dot-red:hover {
  box-shadow: 0 0 5px 0.5px #bf616a;
}

.dot-yellow {
  background: #ebcb8b;
}

.dot-yellow:hover {
  box-shadow: 0 0 5px 0.5px #ebcb8b;
}

.dot-purple {
  background: #b48ead;
}

.dot-purple:hover {
  box-shadow: 0 0 5px 0.5px #b48ead;
}

.tp-code {
  list-style: none;
  font-size: 16px;
  color: #88c0d0;
  font-weight: 600;
  line-height: 20px;
  width: 100%;
}

.tp-code pre {
  line-height: 5px;
}

.tp-code_block {
  margin: 30px 0;
}

.result-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #8fbcbb;
  user-select: none;
}

.tab {
  display: block;
  margin-left: 20px;
  line-height: 10px;
}

.double-tab {
  display: block;
  margin-left: 40px;
  line-height: 10px;
}

.tp-code_list {
  border-radius: 5px 5px 0 0;
}

.result_list {
  border-radius: 0 0 5px 5px;
}

.result::before {
  content: ">  ";
  color: #ebcb8b;
  user-select: none;
}

.result pre {
  display: inline;
}

/* TP-THEMES STYLES */

.tp-themes-block {
  border-left: 3px solid #88c0d0;
}

.tp-themes-block ul {
  list-style-type: none;
}

.tp-themes-list {
  padding-left: 0px;
  position: relative;
  margin-left: -2.65px;
}

.tp-themes-link {
  position: relative;
  display: inline-block;
  margin-left: 10px;
}

.tp-themes-arrow {
  position: absolute;
  right: -30px;
  top: 0.3em;
}

.tp-themes-link::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  display: block;
  transform: translate(-10px, 0.8em);
}

.tp-themes-sublist {
  display: block;
  margin: 11px 0 40px 20px;
  padding: 0 0 0 20px;
  border-left: 2px solid #88c0d0;
  position: relative;
}

.tp-themes-sublist::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 0;
  left: -20px;
}

.tp-themes-subitem {
  position: relative;
  padding-left: 5px;
}

.tp-themes-sublink {
  position: relative;
  display: inline-block;
}

.tp-themes-subitem::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: -20px;
}

.tp-themes-sublink:hover,
.tp-themes-link:hover {
  padding-left: 20px;
  transition: 0.2s linear;
  color: #8fbcbb;
}

.tp-themes-link:hover {
  color: #b48ead;
}

.tp-themes-link:hover::before {
  left: 0px;
}

.tp-themes-sublist::before,
.tp-themes-subitem::before,
.tp-themes-link::before {
  background: #88c0d0;
}

/* Media questions */

@media (max-width: 940px) {
  .tp-title {
    font-size: 22px;
  }
  .tp-link {
    font-size: 13px;
  }
  .tp-sublink {
    font-size: 14px;
  }
  .tp-text {
    font-size: 15px;
  }
  .tp-code {
    font-size: 14px;
  }
  .result-title {
    font-size: 17px;
  }
}

@media (max-width: 820px) {
  .tp-code {
    font-size: 10px;
  }
  .result-title {
    font-size: 15px;
  }
}

@media (max-width: 770px) {
  .tp-themes-link {
    font-size: 15px;
  }
  .tp-themes-sublink {
    font-size: 12px;
    padding-left: 10px;
  }
}

@media (max-width: 740px) {
  .tp-text {
    font-size: 12px;
  }
  .tp-themes-link {
    padding-left: 10px;
  }
  .tp-themes-link::before {
    left: 0px;
  }
}

@media (max-width: 600px) {
  .tp-themes-link {
    font-size: 13px;
  }
}

@media (max-width: 590px) {
  .tp-code {
    font-size: 9px;
    line-height: 12px;
  }
  .tab,
  .double-tab,
  .triple-tab,
  .monster-tab {
    line-height: 3px;
  }
}

@media (max-width: 510px) {
  main {
    padding: 20px 0;
  }
  .main-container {
    border-radius: 0;
  }
}

@media (max-width: 430px) {
  .tp-text {
    font-size: 11px;
  }
  .tp-themes-sublist {
    margin-top: 5px;
  }
  .tp-themes-link {
    font-size: 11px;
  }
  .tp-themes-sublink {
    font-size: 10px;
    padding-left: 5px;
  }
}

/* The end of main styles */
