*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

body {
  min-height: 100vh;
  min-width: 360px;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background: #3b4252;
  padding: 0;
}

a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

.scroll-lock {
  overflow: hidden;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* App.js styles */

.App {
  font-size: 50px;
  font-weight: 800;
  height: 700px;
  display: grid;
  place-items: center;
  background: #2e3440;
}

/* Header styles */

.header {
  width: 100%;
  height: 50px;
  background: #2e3440;
  display: grid;
  justify-content: center;
  grid-template: 1fr / 75%;
  position: relative;
  font-family: "Roboto Mono";
}

.header-container {
  height: 100%;
  display: grid;
  align-content: center;
  grid-template: 1fr / 1fr 1fr;
}

.header-title {
  color: #e5e9f0;
  font-size: 25px;
  font-weight: 800;
  display: inline-block;
  height: 100%;
  line-height: 50px;
  left: 0;
  justify-self: start;
  cursor: pointer;
  transition: 0.2s linear;
}

.header-title:hover {
  text-shadow: 0 0px 5px #e5e9f0;
}

.header-searcher {
  width: 250px;
  position: relative;
  height: 30px;
  justify-self: end;
  align-self: center;
  overflow: hidden;
  border-radius: 10px;
}

#search {
  width: 100%;
  height: 100%;
  border-radius: 30px;
  border: 0;
  background: #e5e9f0;
  color: #2e3440;
  padding: 0 38px 0 15px;
  font-weight: 800;
  position: absolute;
  left: 0;
}

.search-icon_box {
  width: 32px;
  height: 30px;
  cursor: pointer;
  display: grid;
  position: absolute;
  padding-right: 2px;
  right: 0;
  top: 0;
  grid-template: 1fr / 1fr;
  place-items: center;
}

.search-icon {
  display: inline;
  width: 20px !important;
  height: 20px;
  top: 0;
  right: 5px;
  line-height: 30px;
  color: #2e3440;
  text-align: center;
  cursor: pointer;
}

@media (max-width: 590px) {
  .header {
    height: 60px;
  }
  .header-title {
    line-height: 60px;
  }
  .header-searcher {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background: #e5e9f0;
    transition: 0.2s linear;
  }
  .header-searcher:hover {
    box-shadow: 0 0 5px 1px #e5e9f0;
  }
  #search {
    transform: translate(100%, 0);
    display: none;
  }
  .search-icon {
    width: 34px;
    height: 34px;
    left: 0;
    top: 0;
    line-height: 34px;
  }
}

.searcher_active {
  width: 200px;
  border-radius: 30px;
}

.searcher_active #search {
  display: block;
  transform: translate(0, 0);
}

.searcher_active:hover {
  box-shadow: none;
}

@media (max-width: 510px) {
  .header {
    grid-template: 1fr / 1fr;
    padding: 0 20px;
  }
}

/* The end of header styles */

/* Main styles */

main {
  width: 100%;
  height: auto;
  padding: 30px 12.5%;
}

mark {
  background: #8fbcbb;
  color: #f8f9fb;
  padding: 1px;
  border-radius: 2px;
}

.main-container {
  width: 100%;
  height: auto;
  min-height: 500px;
  padding: 10px;
  border-radius: 3px;
  background: #434c5e;
}

.main-item {
  width: 100%;
  height: 100%;
  padding: 0 10px;
  font-family: "Roboto Mono";
  font-weight: 600;
}

/* TP-Styles */

.tp-title {
  font-size: 25px;
  color: #e5e9f0;
}

.tp-link {
  font-size: 16px;
  margin-right: 20px;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-link:hover {
  text-shadow: 0 0 8px #e5e9f0;
}

.tp-link svg {
  margin-right: 5px;
}

.tp-image_block {
  margin: 30px 0 40px 0;
}

.tp-image {
  border-radius: 8px;
  width: 100%;
}

.tp-text {
  font-size: 18px;
  color: #d8dee9;
  white-space: pre-line;
}

.tp-sublinks_block {
  width: 100%;
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
  place-items: center;
  margin-top: 10px;
}

.tp-sublink {
  color: #d8dee9;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-sublink:hover {
  text-shadow: 0 0 8px #d8dee9;
}

.tp-sublink::before {
  content: "#";
  display: inline-block;
  margin-right: 4px;
}

/* TP-Code styles */

.tp-code_block {
  width: 100%;
  height: auto;
  display: block;
}

.tp-code_container {
  width: 100%;
  height: auto;
}

.tp-code_box {
  padding: 26px 5px 5px 5px;
  background: #3b4252;
  border-radius: 8px;
  position: relative;
  border: 2px solid #4c566a;
  box-shadow: 0px 0px 8px 0 #4c566a;
  margin-bottom: 20px;
}

.tp-code_list {
  display: block;
  border-radius: 5px;
  width: 100%;
  background: transparent;
  padding: 10px 10px 20px 10px;
  overflow-x: hidden;
}

.dots {
  position: absolute;
  left: 8px;
  top: 8px;
  display: grid;
  width: 52px;
  grid-template: 1fr / repeat(3, 1fr);
  grid-gap: 8px;
}

.dot-red,
.dot-yellow,
.dot-purple {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s linear;
}

.dot-red {
  background: #bf616a;
}

.dot-red:hover {
  box-shadow: 0 0 5px 0.5px #bf616a;
}

.dot-yellow {
  background: #ebcb8b;
}

.dot-yellow:hover {
  box-shadow: 0 0 5px 0.5px #ebcb8b;
}

.dot-purple {
  background: #b48ead;
}

.dot-purple:hover {
  box-shadow: 0 0 5px 0.5px #b48ead;
}

.tp-code {
  list-style: none;
  font-size: 16px;
  color: #88c0d0;
  font-weight: 600;
  line-height: 20px;
  width: 100%;
}

.tp-code pre {
  line-height: 5px;
}

.tp-code_block {
  margin: 30px 0;
}

.result-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #8fbcbb;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.tab {
  display: block;
  margin-left: 20px;
  line-height: 10px;
}

.double-tab {
  display: block;
  margin-left: 40px;
  line-height: 10px;
}

.tp-code_list {
  border-radius: 5px 5px 0 0;
}

.result_list {
  border-radius: 0 0 5px 5px;
}

.result::before {
  content: ">  ";
  color: #ebcb8b;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.result pre {
  display: inline;
}

/* TP-THEMES STYLES */

.tp-themes-block {
  border-left: 3px solid #88c0d0;
}

.tp-themes-block ul {
  list-style-type: none;
}

.tp-themes-list {
  padding-left: 0px;
  position: relative;
  margin-left: -2.65px;
}

.tp-themes-link {
  position: relative;
  display: inline-block;
  margin-left: 10px;
}

.tp-themes-arrow {
  position: absolute;
  right: -30px;
  top: 0.3em;
}

.tp-themes-link::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  display: block;
  transform: translate(-10px, 0.8em);
}

.tp-themes-sublist {
  display: block;
  margin: 11px 0 40px 20px;
  padding: 0 0 0 20px;
  border-left: 2px solid #88c0d0;
  position: relative;
}

.tp-themes-sublist::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 0;
  left: -20px;
}

.tp-themes-subitem {
  position: relative;
  padding-left: 5px;
}

.tp-themes-sublink {
  position: relative;
  display: inline-block;
}

.tp-themes-subitem::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: -20px;
}

.tp-themes-sublink:hover,
.tp-themes-link:hover {
  padding-left: 20px;
  transition: 0.2s linear;
  color: #8fbcbb;
}

.tp-themes-link:hover {
  color: #b48ead;
}

.tp-themes-link:hover::before {
  left: 0px;
}

.tp-themes-sublist::before,
.tp-themes-subitem::before,
.tp-themes-link::before {
  background: #88c0d0;
}

/* Media questions */

@media (max-width: 940px) {
  .tp-title {
    font-size: 22px;
  }
  .tp-link {
    font-size: 13px;
  }
  .tp-sublink {
    font-size: 14px;
  }
  .tp-text {
    font-size: 15px;
  }
  .tp-code {
    font-size: 14px;
  }
  .result-title {
    font-size: 17px;
  }
}

@media (max-width: 820px) {
  .tp-code {
    font-size: 10px;
  }
  .result-title {
    font-size: 15px;
  }
}

@media (max-width: 770px) {
  .tp-themes-link {
    font-size: 15px;
  }
  .tp-themes-sublink {
    font-size: 12px;
    padding-left: 10px;
  }
}

@media (max-width: 740px) {
  .tp-text {
    font-size: 12px;
  }
  .tp-themes-link {
    padding-left: 10px;
  }
  .tp-themes-link::before {
    left: 0px;
  }
}

@media (max-width: 600px) {
  .tp-themes-link {
    font-size: 13px;
  }
}

@media (max-width: 590px) {
  .tp-code {
    font-size: 9px;
    line-height: 12px;
  }
  .tab,
  .double-tab,
  .triple-tab,
  .monster-tab {
    line-height: 3px;
  }
}

@media (max-width: 510px) {
  main {
    padding: 20px 0;
  }
  .main-container {
    border-radius: 0;
  }
}

@media (max-width: 430px) {
  .tp-text {
    font-size: 11px;
  }
  .tp-themes-sublist {
    margin-top: 5px;
  }
  .tp-themes-link {
    font-size: 11px;
  }
  .tp-themes-sublink {
    font-size: 10px;
    padding-left: 5px;
  }
}

/* The end of main styles */

/* Compact menu styles */

menu {
  padding: 0;
  margin: 0 0 20px 0;
  font-family: "Roboto Mono", monospace;
  position: static;
  top: 0px;
}

.compact-menu {
  display: none;
  width: 100%;
  height: 50px;
  background: #434c5e;
  border-radius: 3px;
  cursor: pointer;
}

.compact-menu_title:hover {
  background: #3b4252;
  color: #d8dee9;
  text-shadow: 0 0 5px #d8dee9;
}

.compact-menu_title {
  width: 100%;
  height: 100%;
  line-height: 50px;
  font-size: 25px;
  color: #e5e9f0;
  text-align: center;
  letter-spacing: 0.2em;
  transition: 0.2s linear;
}

.compact-menu_curtain {
  display: none; /* grid */
  position: absolute;
  background: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  align-content: center;
}

.curtain_active {
  display: grid;
  z-index: 10;
}

.compact-menu_list {
  list-style: none;
  position: relative;
  width: 100%;
  height: auto;
  display: grid;
  grid-template: repeat(4, 1fr) / 1fr;
  padding-left: 0;
  place-content: center;
  grid-gap: 10px;
}

.compact-menu_item {
  height: 50px;
  width: 100%;
  height: 100%;
  display: grid;
  height: 60px;
  text-align: center;
  align-items: center;
}

.compact-menu_link {
  width: 100%;
  height: 100%;
  line-height: 60px;
  font-size: 25px;
  text-shadow: 0 0 2px #e5e9f0;
  transition: 0.2s linear;
}

.compact-menu_link::before {
  margin-right: 15px;
}

.compact-menu_link:hover {
  background: rgba(0, 0, 0, 0.5);
}

.compact-menu_cross {
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  z-index: 0;
  right: 20px;
  top: 20px;
  cursor: pointer;
  transition: 0.3s linear;
}

.compact-menu_cross::before {
  content: "";
  display: block;
  width: 3px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px #e5e9f0;
  background: #e5e9f0;
  top: 0;
  left: 0;
  margin-left: 50%;
  transform: rotate(45deg);
}

.compact-menu_cross::after {
  content: "";
  display: block;
  width: 3px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px #e5e9f0;
  background: #e5e9f0;
  margin: -100% 0 0 50%;
  transform: rotate(-45deg);
}

/* Apply this */
@media (max-width: 590px) {
  .compact-menu {
    display: grid;
  }
}

@media (max-width: 510px) {
  .compact-menu {
    border-radius: 0;
  }
}

@media (max-width: 470px) {
  .compact-menu {
    height: 40px;
  }
  .compact-menu_title {
    line-height: 40px;
    font-size: 20px;
  }
}

/* The end of compact menu styles */

/* Menu styles */

a {
  text-decoration: none;
  color: #d8dee9;
}

.menu {
  width: 100%;
  height: 50px;
  display: block;
  position: relative;
  margin-bottom: 15px;
}

.menu-list {
  width: 100%;
  height: 100%;
  list-style: none;
  display: grid;
  grid-template: 1fr / repeat(4, 1fr);
  padding-left: 0;
  border-radius: 5px;
  overflow: hidden;
}

.menu-item {
  height: 100%;
  width: 100%;
  background: #2e3440;
  justify-self: start;
  text-align: center;
  line-height: 50px;
  color: #d8dee9;
  font-weight: 800;
  cursor: pointer;
  transition: 0.2s linear;
  font-family: "Roboto Mono", monospace;
}

.menu-item:hover {
  background: #3b4252;
}

.menu-link {
  display: block;
  width: 100%;
  height: 100%;
}

/* Media questions */

@media (max-width: 940px) {
  .menu {
    height: 40px;
  }
  .menu-item {
    line-height: 40px;
  }
  .menu-link {
    font-size: 13px;
  }
}

@media (max-width: 740px) {
  .menu {
    height: 36px;
  }
  .menu-item {
    line-height: 36px;
  }
  .menu-link {
    font-size: 11px;
  }
}

@media (max-width: 590px) {
  .menu {
    display: none;
  }
}

/* The end of menu styles */

/* Footer styles */

footer {
  width: 100%;
  height: auto;
  padding: 0 12.5% 30px 12.5%;
  font-family: "Roboto Mono", monospace;
  z-index: 2;
}

.footer_container {
  width: 100%;
  height: 300px;
  background: #434c5e;
  border-radius: 3px;
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
}

.footer-item {
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center;
  position: relative;
  grid-template: 1fr / 1fr;
  padding: 20px;
}

.footer-menu-list {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: repeat(4, 1fr);
  list-style: none;
  place-self: center;
  padding-left: 0;
}

.footer-menu-title {
  font-size: 22px;
  font-weight: 800;
  text-align: center;
  color: #8fbcbb;
}

.footer-menu-item {
  text-align: center;
  font-size: 20px;
  font-weight: 800;
  display: grid;
  place-content: center;
  position: relative;
  grid-template: 1fr / 1fr;
}

.footer-menu-link {
  width: 100%;
  height: 100%;
  display: inline-block;
  line-height: 50px;
  transition: 0.2s linear;
  border-radius: 5px;
  z-index: 2;
}

.footer-menu-link:hover {
  background: #3b4252;
}

/* Media questions */

